import {HTTP} from '@/common/http-common'

export default {
  state: {
    user: null,
    helloNetwork: null
  },

  getters: {
    user (state) {
      if (state.user == null) {
        state.user = JSON.parse(localStorage.getItem('user'))
      }
      return state.user
    },

    helloNetwork (state) {
      if (state.helloNetwork == null) {
        state.helloNetwork = localStorage.getItem('helloNetwork')
      }
      return state.helloNetwork
    },

    isLoggedIn (state, getters) {
      return getters.user != null
    },

    token (state, getters) {
      return getters.user ? getters.user.accessToken : null
    },

    helloToken (state, getters) {
      if (state.helloToken == null) {
        state.helloToken = JSON.parse(localStorage.getItem('hello')) ? JSON.parse(localStorage.getItem('hello'))[getters.helloNetwork].access_token : null
      }
      return state.helloToken
    }
  },

  mutations: {
    user (state, payload) {
      state.user = payload
      localStorage.setItem('user', JSON.stringify(state.user))
    },
    logout (state) {
      localStorage.removeItem('user')
      localStorage.removeItem('hello')
    },
    helloNetwork (state, payload) {
      state.helloNetwork = payload
      localStorage.setItem('helloNetwork', state.helloNetwork)
    }
  },

  actions: {
    authenticate ({commit, getters}, payload) {
      console.log('in authenticate', payload)
      var imageUrl = payload.image ? payload.image.url : null
      var t = getters.helloToken
      var networkName = getters.helloNetwork
      var networkCode = networkName === 'google' ? 1 : 2
      var reqObj = {
        thirdPartyUserId: payload.id,
        name: payload.name,
        image: imageUrl ? encodeURI(imageUrl) : null,
        email: payload.email,
        gender: payload.gender,
        thirdPartyAccessToken: t,
        GrantType: 'implicit',
        notificationPlayerId: payload.notificationPlayerId,
        tokenProvider: networkCode
      }

      commit('isLoading', true)
      HTTP.post('token', reqObj).then(response => {
        commit('user', response.data)
        commit('isLoading', false)
      }).catch(e => {
        commit('error', e)
        commit('isLoading', false)
      })
    },

    logout ({commit}) {
      commit('logout', null)
    },

    helloNetwork ({commit}, payload) {
      commit('helloNetwork', payload)
    }
  }
}



